/* 미디어 쿼리 추가 (옵션) */
@media (max-width: 768px) {
    .main {
        width: 1rem auto;
    }
}

.main {
    display: flex;
    flex-direction: column;
    margin: 10rem auto;
    width: 30rem;
    padding: 1rem;
}

.header {
    margin-top: 2rem;
    
}
.menu {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}
.form_body {
    margin: 2rem 0;
    display: flex;
    flex-direction: column;
    justify-content:space-around;
}
.form_title {
    width: calc(100% - 2.2rem);
    height: 1rem;
	padding: 1rem;
    border: solid 2px #1E90FF;
	border-radius: 5px;
}

.form_textbox {
    width: 100%;
	height: 10rem;
	padding: 1rem;
	box-sizing: border-box;
	border: solid 2px #1E90FF;
	border-radius: 5px;
	font-size: 16px;
	resize: both;
    margin: 2rem auto;
		
}
.form_footer {
    display: flex;
    justify-content: end;
    margin-top: 2rem 0;
}

.form_footer button {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    border-radius: 5rem;
    border: none;
    width: 4rem;
    padding: 0.3rem 0;
    margin-right: 0.5rem;
}